import Lines from '@/components/common/Lines.vue';
import $http from '@/common/api/request.js'
export default {
	data() {
		return {
			userTel:'',
			//验证的规格
			rules:{
				userTel:{
					rule:/^1[3456789]\d{9}$/,
					msg:"请输入11位手机号"
				}
			}
		}
	},
	components:{
		Lines
	},
	methods: {
		//判断验证是否符合要求
		validate(key){
			let bool = true;
			if(  !this.rules[key].rule.test(this[key]) ){
				uni.showToast({
					title:this.rules[key].msg,
					icon:"none"
				})
				bool=false;
				return false;
			}
			return bool;
		},
		goNextCode(){
			if(  !this.validate('userTel')  ) return;
			$http.request({
				url:"/registered",
				method:"POST",
				data:{
					phone:this.userTel
				}
			}).then((res)=>{
				if(!res.success){
					uni.showToast({
						title:res.msg,
						icon:"none"
					})
					return;
				}else{
					uni.navigateTo({
						url:"../login-code/login-code?phone="+this.userTel
					})
				}
			})
			
		}
	}
}
